import React from 'react';

const CountryInfo = ({ data, fatArea, detaild }) => {
    let Name = data.name.common;
    let Area = data.area;
    
return (

    <div>
      <span className="bold"> {Name} </span>
      <span className="notBold"> 
      {Area}Km<sup>2</sup>
      </span>
    <div 
    className="bar"
    style={{ width: +(data.area / fatArea) * 50 + "%" }}
    
    ></div>

    { detaild && (
      <div>
      Capital: {data.capital} <br></br> 
      Region: {data.region}
      </div>
    )}
    </div>
);
};

export default CountryInfo;
    


